import { Component, Prop, h } from '@stencil/core';
import { isExternalUrl } from '../../../utils';
import { MENU_ITEM_AS, MENU_ITEM_VARIANTS } from './export';

@Component({
  tag: 'mdx-login-flyout-menu-item',
  styleUrl: 'login-flyout-menu-item.scss',
  shadow: true,
})
export class LoginFlyoutMenuItem {
  @Prop()
  href: string;

  @Prop()
  label: string;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  @Prop({ reflect: true })
  variant: (typeof MENU_ITEM_VARIANTS)[keyof typeof MENU_ITEM_VARIANTS] = MENU_ITEM_VARIANTS.CUSTOM;

  @Prop({ reflect: true })
  as: (typeof MENU_ITEM_AS)[keyof typeof MENU_ITEM_AS] = MENU_ITEM_AS.LINK;

  @Prop({ reflect: true })
  dark: boolean;

  @Prop()
  menuItemClick: () => void;

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    if (this.as === MENU_ITEM_AS.LINK) {
      return (
        <a
          class="login-flyout-menu-item"
          href={this.href}
          target={this.target}
          rel={this.isExternalURL() ? 'noreferrer' : undefined}
          onClick={this.menuItemClick}
        >
          {this.variant === MENU_ITEM_VARIANTS.ACCOUNT ? (
            <span class="login-flyout-menu-item__account">
              <mdx-icon-migros-filled />
              {this.label}
            </span>
          ) : (
            <span class="login-flyout-menu-item__label">{this.label}</span>
          )}
        </a>
      );
    }

    return (
      <button class="login-flyout-menu-item" onClick={this.menuItemClick}>
        <span class="login-flyout-menu-item__label">{this.label}</span>
      </button>
    );
  }
}
