:host([variant='custom']) .login-flyout-menu-item {
  all: unset;
  font: var(--mdx-comp-login-flyout-typography-desktop-menu-items-custom-link-default);
  color: var(--mdx-comp-login-flyout-color-light-menu-items-custom-link-color-default);
  text-decoration: none;
  display: flex;
  align-items: center;
  cursor: pointer;
  word-break: break-all;
  width: 100%;
  height: var(--mdx-comp-login-flyout-sizing-desktop-menu-items-custom-link-height);
  background-color: var(--mdx-comp-login-flyout-color-light-menu-items-custom-link-background-default);

  &:hover {
    font: var(--mdx-comp-login-flyout-typography-desktop-menu-items-custom-link-hover);
    color: var(--mdx-comp-login-flyout-color-light-menu-items-custom-link-color-hover);
    background-color: var(--mdx-comp-login-flyout-color-light-menu-items-custom-link-background-hover);
  }

  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
  }

  &__label {
    display: block;
    padding-left: var(--mdx-comp-login-flyout-spacing-desktop-menu-items-custom-link-padding-left);
    padding-right: var(--mdx-comp-login-flyout-spacing-desktop-menu-items-custom-link-padding-right);
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}

:host([variant='account']) .login-flyout-menu-item {
  all: unset;
  font: var(--mdx-comp-login-flyout-typography-desktop-menu-items-account-link-default);
  color: var(--mdx-comp-login-flyout-color-light-menu-items-account-link-color-default);
  text-decoration: none;
  width: 100%;
  height: var(--mdx-comp-login-flyout-sizing-desktop-menu-items-account-link-heigh);
  cursor: pointer;
  word-break: break-all;
  display: block;

  &:hover {
    font: var(--mdx-comp-login-flyout-typography-desktop-menu-items-account-link-hover);
    color: var(--mdx-comp-login-flyout-color-light-menu-items-account-link-color-hover);
  }

  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
  }

  &__account {
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    justify-content: start;
    gap: var(--mdx-comp-login-flyout-spacing-desktop-menu-items-account-link-gap-horizontal);
    padding-right: var(--mdx-comp-login-flyout-spacing-desktop-menu-items-account-link-padding-right);
    padding-left: var(--mdx-comp-login-flyout-spacing-desktop-menu-items-account-link-padding-left);
    height: var(--mdx-comp-login-flyout-sizing-desktop-menu-items-account-link-height);
    background-color: var(--mdx-comp-login-flyout-color-light-menu-items-account-link-background-default);

    @media screen and (max-width: 768px) {
      padding-right: var(--mdx-comp-login-flyout-spacing-mobile-menu-items-account-link-padding-right);
      padding-left: var(--mdx-comp-login-flyout-spacing-mobile-menu-items-account-link-padding-left);
      font: var(--mdx-comp-login-flyout-typography-mobile-menu-items-account-link-default);
    }

    &:hover {
      background-color: var(--mdx-comp-login-flyout-color-light-menu-items-account-link-background-hover);
    }
  }
}

:host([dark][variant='account']) .login-flyout-menu-item {
  &__account {
    background-color: var(--mdx-comp-login-flyout-color-dark-menu-items-account-link-background-default);
    color: var(--mdx-comp-login-flyout-color-dark-menu-items-account-link-color-default);

    &:hover {
      background-color: var(--mdx-comp-login-flyout-color-dark-menu-items-account-link-background-hover);
      color: var(--mdx-comp-login-flyout-color-dark-menu-items-account-link-color-hover);
    }
  }
}

:host([dark][variant='custom']) .login-flyout-menu-item {
  background-color: var(--mdx-comp-login-flyout-color-dark-menu-items-custom-link-background-default);
  color: var(--mdx-comp-login-flyout-color-dark-menu-items-custom-link-color-default);

  &:hover {
    background-color: var(--mdx-comp-login-flyout-color-dark-menu-items-custom-link-background-hover);
    color: var(--mdx-comp-login-flyout-color-dark-menu-items-custom-link-color-hover);
  }
}
